import React from "react";

interface ChatListProps {
  conversations: any[];
  active?: string;
  onSelect: (wa_id: string) => void;
}

export default function ChatList({ conversations, active, onSelect }: ChatListProps) {
  return (
    <aside className="hidden md:flex flex-col w-80 border-r border-wa-border bg-wa-sidebar">
      {/* Header */}
      <div className="h-16 flex items-center px-4 border-b border-wa-border bg-wa-header">
        <h1 className="font-semibold text-lg">WhatsApp</h1>
      </div>

      {/* Chat Items */}
      <div className="flex-1 overflow-y-auto">
        {conversations.map((c) => (
          <button
            key={c._id || c.wa_id}
            onClick={() => onSelect(c._id || c.wa_id)}
            className={`w-full px-4 py-3 flex items-center gap-3 hover:bg-gray-200 ${
              active === (c._id || c.wa_id) ? "bg-gray-300" : ""
            }`}
          >
            <div className="flex-1 text-left">
              <div className="font-medium">{c.name || c._id}</div>
              <div className="text-sm text-gray-500 truncate">{c.last_message}</div>
            </div>
          </button>
        ))}
      </div>
    </aside>
  );
}
