import React, { useEffect, useState } from "react";
import dayjs from "dayjs";
import { fetchMessages, sendMessage } from "../lib/api";

interface ChatWindowProps {
  wa_id?: string;
  name?: string;
  socketUpdater: (cb: (m: any) => void) => void;
}

export default function ChatWindow({ wa_id, name }: ChatWindowProps) {
  const [messages, setMessages] = useState<any[]>([]);
  const [text, setText] = useState("");

  useEffect(() => {
    if (wa_id) {
      fetchMessages(wa_id).then(setMessages).catch(console.error);
    }
  }, [wa_id]);

  const handleSend = async () => {
    if (!text.trim() || !wa_id) return;
    const newMsg = {
      wa_id,
      name: name || "You",
      from: "you",
      text,
      timestamp: new Date().toISOString(),
      status: "sent",
    };
    await sendMessage(newMsg);
    setMessages((prev) => [...prev, newMsg]);
    setText("");
  };

  return (
    <div className="flex flex-col h-screen bg-wa-chatbg">
      {/* Header */}
      <div className="h-16 px-4 flex items-center border-b border-wa-border bg-wa-header">
        <h2 className="font-medium">{name || "Select a chat"}</h2>
      </div>

      {/* Messages */}
      <div className="flex-1 overflow-y-auto p-4 space-y-2">
        {messages.map((m, i) => (
          <div
            key={m._id || i}
            className={`max-w-xs px-3 py-2 rounded-lg text-sm shadow ${
              m.from === "you"
                ? "bg-wa-outgoing ml-auto"
                : "bg-wa-incoming mr-auto"
            }`}
          >
            <p>{m.text}</p>
            <span className="block text-[10px] text-gray-500 mt-1">
              {dayjs(m.timestamp).format("HH:mm")}
            </span>
          </div>
        ))}
      </div>

      {/* Input */}
      {wa_id && (
        <div className="p-3 border-t border-wa-border bg-wa-header flex gap-2">
          <input
            value={text}
            onChange={(e) => setText(e.target.value)}
            onKeyDown={(e) => e.key === "Enter" && handleSend()}
            placeholder="Type a message"
            className="flex-1 px-3 py-2 border border-gray-300 rounded-full text-sm focus:outline-none"
          />
          <button
            onClick={handleSend}
            className="bg-green-500 text-white px-4 py-2 rounded-full"
          >
            Send
          </button>
        </div>
      )}
    </div>
  );
}
